<template>
    <div class="m-archive" v-loading="loading">

        <!-- 搜索 -->
        <div class="m-archive-search" slot="search-before">
            <!-- <a :href="publish_link" class="u-publish el-button el-button--primary">+ 发布作品</a> -->
            <el-input placeholder="请输入搜索内容" v-model="search" class="input-with-select">
                <span slot="prepend">关键词</span>
                <!-- <el-select
                    v-model="searchType"
                    slot="prepend"
                    placeholder="请选择"
                >
                    <el-option label="标题" value="title"></el-option>
                    <el-option label="作者" value="authorname"></el-option>
                </el-select>-->
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
        </div>

        <el-tabs v-model="subtype" class="m-archive-tabs">
            <el-tab-pane label="全部" name="0">
                <span slot="label">
                    <i class="u-icon el-icon-menu"></i> 全部作品
                </span>
            </el-tab-pane>
            <el-tab-pane :label="item.label" :name="key" v-for="(item,key) in subtypes" :key="key">
                <span slot="label">
                    <i :class="key | showSubtypeIcon" class="u-icon"></i>
                    {{item.label}}
                </span>
            </el-tab-pane>
        </el-tabs>

        <listbox
            :data="data"
            :total="total"
            :pages="pages"
            :per="per"
            :page="page"
            @appendPage="appendPage"
            @changePage="changePage"
        >
            <!-- 过滤 -->
            <template slot="filter">
                <!-- 版本过滤 -->
                <clientBy @filter="filter" :type="client"></clientBy>
                <!-- 角标过滤 -->
                <markBy @filter="filter"></markBy>
                <!-- 排序过滤 -->
                <orderBy @filter="filter"></orderBy>
            </template>

            <!-- 列表 -->
            <div class="m-archive-list" v-if="data.length">
                <ul class="u-list">
                    <li class="u-item" v-for="(item, i) in data" :key="i">
                        <!-- Banner -->
                        <a class="u-banner" :href="item.ID | postLink" :target="target">
                            <img
                                :src="
                                    showBanner(
                                        item.post_banner,
                                        item.post_subtype
                                    )
                                "
                            />
                        </a>

                        <h2 class="u-post" :class="{ isSticky: item.sticky }">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>

                            <!-- 标题文字 -->
                            <a
                                class="u-title"
                                :style="item.color | isHighlight"
                                :href="item.ID | postLink"
                                :target="target"
                            >{{ item.post_title || "无标题" }}</a>

                            <!-- 角标 -->
                            <span class="u-marks" v-if="item.mark && item.mark.length">
                                <i
                                    v-for="mark in item.mark"
                                    class="u-mark"
                                    :class="mark | markcls"
                                    :key="mark"
                                >{{ mark | showMark }}</i>
                            </span>
                        </h2>

                        <!-- 字段 -->
                        <div class="u-content u-desc">{{ item.post_excerpt || item.post_title }}</div>

                        <!-- 作者 -->
                        <div class="u-misc">
                            <img
                                class="u-author-avatar"
                                :src="item.author_info.user_avatar | showAvatar"
                                :alt="item.author_info.display_name"
                            />
                            <a
                                class="u-author-name"
                                :href="item.post_author | authorLink"
                                target="_blank"
                            >{{ item.author_info.display_name }}</a>
                            <span class="u-date">
                                Updated on
                                <time
                                    v-if="order == 'update'"
                                >{{item.post_modified | dateFormat}}</time>
                                <time v-else>{{item.post_date | dateFormat}}</time>
                            </span>
                        </div>
                    </li>
                </ul>
            </div>
        </listbox>
    </div>
</template>

<script>
import subtypes from "@/assets/data/bbs_types.json";
import listbox from "@jx3box/jx3box-page/src/cms-list.vue";
import { cms as mark_map } from "@jx3box/jx3box-common/data/mark.json";
import _ from "lodash";
import { getPosts } from "../service/post";
import dateFormat from "../utils/dateFormat";
import {
    __ossMirror,
    __imgPath,
    __ossRoot,
    __Root,
} from "@jx3box/jx3box-common/data/jx3box";
import {
    showAvatar,
    authorLink,
    showBanner,
    publishLink,
    buildTarget,
    getAppType,
} from "@jx3box/jx3box-common/js/utils";
export default {
    name: "list",
    props: [],
    data: function () {
        return {
            loading: false, //加载状态

            data: [], //数据列表
            page: 1, //当前页数
            total: 1, //总条目数
            pages: 1, //总页数
            per: 10, //每页条目
            appendMode: false, //追加模式

            search: "",
            searchType: "title",

            order: "update", //排序模式
            mark: "", //筛选模式
            client: this.$store.state.client, //版本选择

            subtype: "",
            subtypes,
        };
    },
    computed: {
        // subtype: function () {
        //     return this.$route.params.subtype;
        // },
        resetParams: function () {
            return [this.subtype, this.search, this.mark, this.client];
        },
        params: function () {
            let params = {
                per: this.per,
                page: ~~this.page || 1,
                sticky: 1,
                type: "bbs",
            };
            let optionalParams = ["search", "order", "mark", "client"];
            optionalParams.forEach((item) => {
                if (this[item]) {
                    params[item] = this[item];
                }
            });

            if (!!~~this.subtype) {
                params.subtype = this.subtype;
            }

            return params;
        },
        target: function () {
            return buildTarget();
        },
        // 根据栏目定义
        defaultBanner: function () {
            return __imgPath + "image/banner/null.png";
        },
        publish_link: function (val) {
            return publishLink("bbs");
        },
    },
    methods: {
        loadPosts: function () {
            this.loading = true;
            getPosts(this.params, this)
                .then((res) => {
                    if (this.appendMode) {
                        this.data = this.data.concat(res.data.data.list);
                    } else {
                        this.data = res.data.data.list;
                    }
                    this.total = res.data.data.total;
                    this.pages = res.data.data.pages;
                })
                .finally(() => {
                    this.appendMode = false;
                    this.loading = false;
                });
        },
        changePage: function (i) {
            this.appendMode = false;
            this.page = i;
            window.scrollTo(0, 0);
        },
        appendPage: function (i) {
            this.appendMode = true;
            this.page = i;
        },
        filter: function (o) {
            this.appendMode = false;
            this[o["type"]] = o["val"];
        },
        showBanner: function (val, subtype) {
            if (val) {
                return showBanner(val);
            } else {
                return __imgPath + "image/banner/bbs" + subtype + ".png?v=1";
            }
            return this.defaultBanner;
        },
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val));
        },
        showAvatar: function (val) {
            return showAvatar(val);
        },
        authorLink: function (val) {
            return authorLink(val);
        },
        postLink: function (val) {
            return location.origin + "/bbs/" + val;
        },
        isHighlight: function (val) {
            return val ? `color:${val};font-weight:600;` : "";
        },
        showMark: function (val) {
            return mark_map[val];
        },
        markcls: function (val) {
            return "u-mark-" + val;
        },
        showSubtypeIcon: function (val) {
            return subtypes[val]["icon"];
        },
    },
    watch: {
        subtype: function () {
            this.search = "";
        },
        "$route.params.subtype": function (val) {
            this.$store.state.subtype = val;
            this.subtype = val;
        },
        "$route.query.page": {
            immediate: true,
            handler: function (val) {
                this.page = ~~val || 1;
            },
        },
        "$route.query.search": {
            immediate: true,
            handler: function (val) {
                this.search = val;
            },
        },
        params: {
            deep: true,
            // immediate: true,
            handler: function (val) {
                this.loadPosts();
            },
        },
    },
    mounted: function () {
        this.loadPosts();
    },
    components: {
        listbox,
    },
};
</script>

<style lang="less">
@import "../assets/css/list.less";
</style>
